import * as React from "react";
import logo1 from "../../assets/logo_web-01.png";
import logo2 from "../../assets/logo_web-02.png";
import logo3 from "../../assets/logo_web-03.png";
import logo4 from "../../assets/logo_web-04.png";
import logo5 from "../../assets/logo_web-05.png";
import logo6 from "../../assets/logo_web-06.png";
import styled from "styled-components";

export interface BrandDocProps {}

const BrandDocBox = styled.div`
    display: flex;
    align-items: flex-end;
    justify-content: center;
    width: 100%;
    height: 200px;

    & > div {
        width: 16%;
        padding-bottom: 16%;
        height: 0;
        position: relative;
        transform: scale(1);
        transition: all 0.3s;
        display: flex;

        & > img {
            position: absolute;
            width: 70%;
            height: 70%;
            top: 0;
            left: 0;
            border-radius: 50%;
        }

        &.trans-4 {
            transform: scale(1.28);
            margin-bottom: 1%;
            width: calc(16% * 1.28);
            padding-bottom: calc(16% * 1.28);
        }

        &.trans-3 {
            transform: scale(1.21);
            margin-bottom: 0.6%;
            width: calc(16% * 1.21);
            padding-bottom: calc(16% * 1.21);
        }

        &.trans-2 {
            transform: scale(1.14);
            margin-bottom: 0.4%;
            width: calc(16% * 1.14);
            padding-bottom: calc(16% * 1.14);
        }
        &.trans-1 {
            transform: scale(1.07);
            margin-bottom: 0.1%;
            width: calc(16% * 1.07);
            padding-bottom: calc(16% * 1.07);
        }
    }
`;

export default class BrandDoc extends React.Component<BrandDocProps, any> {
    public childs: HTMLDivElement[] = [];
    public onMouseOver = idx => e => {
        this.childs[idx].className = "trans-4";
        if (idx - 1 >= 0) {
            this.childs[idx - 1].className = "trans-3";
        }
        if (idx - 2 >= 0) {
            this.childs[idx - 2].className = "trans-2";
        }
        if (idx - 3 >= 0) {
            this.childs[idx - 3].className = "trans-1";
        }

        if (idx + 1 < this.childs.length) {
            this.childs[idx + 1].className = "trans-3";
        }

        if (idx + 2 < this.childs.length) {
            this.childs[idx + 2].className = "trans-2";
        }

        if (idx + 3 < this.childs.length) {
            this.childs[idx + 3].className = "trans-1";
        }
    };

    public onMouseOut = () => {
        this.childs.map(c => {
            c.className = "";
        });
    };
    public render() {
        return (
            <BrandDocBox>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(0)} onMouseOut={this.onMouseOut}>
                    <img src={logo1} alt="" />
                </div>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(1)} onMouseOut={this.onMouseOut}>
                    <img src={logo2} alt="" />
                </div>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(2)} onMouseOut={this.onMouseOut}>
                    <img src={logo3} alt="" />
                </div>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(3)} onMouseOut={this.onMouseOut}>
                    <img src={logo4} alt="" />
                </div>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(4)} onMouseOut={this.onMouseOut}>
                    <img src={logo5} alt="" />
                </div>
                <div ref={(ref: HTMLDivElement) => this.childs.push(ref)} onMouseOver={this.onMouseOver(5)} onMouseOut={this.onMouseOut}>
                    <img src={logo6} alt="" />
                </div>
            </BrandDocBox>
        );
    }
}



