import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";
import Banner from "src/components/Banner";
import BrandDoc from "src/components/BrandDoc/BrandDoc";

export interface IndexProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    display: flex;
    flex-direction: column;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;

    align-items: center;

    & > * {
        width: 70%;
    }
    & > p {
        margin: 0;

        &:nth-child(1) {
            font-size: 32px;
            font-weight: bold;

            margin-bottom: 60px;
        }

        &:nth-child(2) {
            font-size: 24px;
            font-weight: bold;

            margin-bottom: 60px;
        }
    }

    & > img {
        margin-bottom: 100px;
    }

    & > div {
        margin-bottom: 60px;
        & > *:nth-child(1) {
            font-size: 34px;
            font-weight: bold;

            margin-bottom: 32px;
        }

        & > *:nth-child(2) {
            font-size: 24px;
        }
    }
`;

export default class Index extends React.Component<IndexProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "브랜드", link: "/brand/" }, { name: "브랜드 기획", link: "/planning" }]}
                />
                <Content>
                    <Banner>
                        <img src={require("../../assets/brand/brand/brand-banner.png")} alt="" />
                        <div className="desc typing">
                            <h1 className="typing" lang="en">
                                Always Planning
                            </h1>
                            <h2>유니언플레이스 브랜드전략실은</h2>
                            <h2>늘 변함 없는 가치를 기획의 본질로 삼습니다.</h2>
                        </div>
                    </Banner>
                    <Desc>
                        <p>유니언 플레이스의 브랜드를 만듭니다.</p>
                        <p>
                            기획(企劃)이라는 글자에는 사람(人)이 있습니다. <br />
                            사람을 향하는 마음은 시간이 흘러도 변하지 않습니다. <br />
                            그래서 우리 기획의 본질은 ‘사람’입니다.
                        </p>
                        <img src={require("../../assets/brand/brand/brand01.png")} alt="" />
                        <div>
                            <p lang="en">Simplicity</p>
                            <p>
                                우리의 기획은 쉽습니다. <br />
                                현상은 복잡하지만, 본질은 단순하기 때문입니다.
                            </p>
                        </div>
                        <div>
                            <p lang="en">Fun</p>
                            <p>
                                우리의 기획은 재미있습니다. <br />
                                자유로운 환경에서 ‘즐거움’을 발견하기 때문입니다.
                            </p>
                        </div>
                        <div>
                            <p lang="en">Touch</p>
                            <p>
                                우리의 기획은 울림이 있습니다. <br />
                                마음을 움직이는 스토리가 있기 때문입니다.
                            </p>
                        </div>
                        <div>
                            <p lang="en" />
                            <p>
                                <BrandDoc />
                            </p>
                        </div>
                        <div>
                            <iframe
                                width="100%"
                                height="530px"
                                src="https://www.youtube.com/embed/Kn91U_q4P1g"
                                style={{ marginTop: 10, marginBottom: 10 }}
                            />
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



